import Vue from 'vue';
import VueRouter from 'vue-router';
import VueResource from 'vue-resource';

//路由文件
import Index from './routers/indexRouter.vue';

Vue.use(VueRouter);
Vue.use(VueResource);

Vue.http.options.emulateJSON = true;

var router = new VueRouter();

router.map({
    '/index': {
        name: 'index',
        component: Index
    }
});

router.beforeEach(function(){
    window.scrollTo(0,0);
});

router.afterEach(function(){

});

router.redirect({
    '*': '/index'
});

var App = Vue.extend({});
router.start(App,'#app');



